import React, { useState, useEffect } from 'react'
import Select from 'react-select';
import Prices from './components/Prices';
import Details from './components/Details';
import PriceService from './services/PriceService';


const App = (props) => {
  const [prices, setPriceData] = useState([]);
  const [priceData, updatePriceData] = useState('');
  
  useEffect(() => {
    document.title = 'Gasoline Prices';
    PriceService.getAll().then(results => {
      setPriceData(results);
    });
  }, []);
  
  const stateList = [];
  prices.forEach(element => {
    stateList.push(element.state);
  });
 
  const createStateList = () => stateList.map(state => {
      return {value: state, label: state};
    });

  const changePrices = (option) => {
    updatePriceData(prices.find(priceItem => priceItem.state === option.value));
  };

  return (
    <div>
      <h1> Gasoline Prices </h1>
      <Select 
      options={createStateList()}
      onChange={option => changePrices(option)}
      />
     <Prices data={priceData} />
     <Details />
    </div>
  );
}


export default App;
